<style>
.m-carManage-carInfo {
  padding: 20px;
}
.m-carManage-carInfo-header {
  margin-bottom: 20px;
}
.m-carManage-carInfo-dialog .el-input {
  width: 300px;
}
.m-carManage-carInfo-dialog .el-form {
  padding: 20px;
}
.m-carManage-carInfo-apply-dialog .el-input {
  width: 300px;
}
.m-carManage-carInfo-apply-dialog .el-form {
  padding: 20px;
}
</style>
<template>
  <div class="m-carManage-carInfo">
    <div class="m-carManage-carInfo-header">
      <el-button class="add_btn" type="primary" @click="onAddEvent">新增车辆信息</el-button>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :width="col.width">
      </el-table-column>
      <el-table-column
        label="操作"
        width="200">
        <template slot-scope="scope">
          <!-- <el-button
            @click.native.prevent="onApplyEvent(scope.$index)"
            type="text">
            车辆申请
          </el-button> -->
          <el-button
            @click.native.prevent="onEditEvent(scope.$index)"
            type="text">
            编辑
          </el-button>
          <el-button
            @click.native.prevent="onDelEvent(scope.$index)"
            type="text">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog class="m-carManage-carInfo-dialog" append-to-body :title="isEdit?'编辑车辆信息':'新增车辆信息'" :visible.sync="showEditDialog">
      <el-form :model="item" :rules="rules" ref="itemForm" label-position="right" label-width="100px">
        <el-form-item v-if="isEdit" label="ID">
          <el-input v-model="item.car_id" auto-complete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="车牌号码" prop="car_license">
          <el-input v-model="item.car_license" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="车辆品牌" prop="brand">
          <el-input v-model="item.brand" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="型号" prop="model">
          <el-input v-model="item.model" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="颜色" prop="color">
          <el-input v-model="item.color" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="车架号" prop="frame_number">
          <el-input v-model="item.frame_number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="发动机型号" prop="engine_number">
          <el-input v-model="item.engine_number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="车辆状态" prop="car_status">
          <el-select v-model="item.car_status" placeholder="请选择">
            <el-option label="拖车" :value="0"></el-option>
            <el-option label="押车" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否可借用" prop="borrow_status">
          <el-select v-model="item.borrow_status" placeholder="请选择">
            <el-option label="是" :value="0"></el-option>
            <el-option label="否" :value="1"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showEditDialog = false">取 消</el-button>
        <el-button type="primary" @click="onDialogComfirm">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog class="m-carManage-carInfo-apply-dialog" append-to-body title="车辆申请" :visible.sync="showApplyDialog">
      <el-form :model="applyItem" :rules="rules_apply" ref="applyItemForm" label-position="right" label-width="100px">
        <el-form-item label="车辆ID">
          <el-input v-model="applyItem.car_id" auto-complete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="申请用途" prop="use">
          <el-input v-model="applyItem.use" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="开始时间" prop="start">
          <el-date-picker type="date" placeholder="选择日期" value-format="yyyy-MM-dd" v-model="applyItem.start" style="width: 300px;"></el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间" prop="end">
          <el-date-picker type="date" placeholder="选择日期" value-format="yyyy-MM-dd" v-model="applyItem.end" style="width: 300px;"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showApplyDialog = false">取 消</el-button>
        <el-button type="primary" @click="onApplyDialogComfirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { equalObject } from '@/common/utils'

export default {
  data(){
    return {
      cols:[
        {
          prop: 'car_id',
          label: 'ID'
        },{
          prop: 'car_license',
          label: '车牌号码'
        },{
          prop: 'brand',
          label: '车辆品牌'
        },{
          prop: 'model',
          label: '型号'
        },{
          prop: 'color',
          label: '颜色'
        },{
          prop: 'frame_number',
          label: '车架号'
        },{
          prop: 'engine_number',
          label: '发动机号'
        },{
          prop: 'car_status',
          label: '车辆状态'
        },{
          prop: 'borrow_status',
          label: '是否可借用'
        }
      ],
      rows: [],
      item: {},
      rules: {
        car_license:[
          {required: true, message: '请输入车牌号码', trigger: 'blur'}
        ],
        brand:[
          {required: true, message: '请输入车辆品牌', trigger: 'blur'}
        ],
        model:[
          {required: true, message: '请输入车辆型号', trigger: 'blur'}
        ],
        color:[
          {required: true, message: '请输入车辆颜色', trigger: 'blur'}
        ],
        frame_number:[
          {required: true, message: '请输入车架号', trigger: 'blur'}
        ],
        engine_number:[
          {required: true, message: '请输入发动机号', trigger: 'blur'}
        ],
        car_status:[
          {type: 'number', required: true, message: '请选择车辆状态', trigger: 'change'}
        ],
        borrow_status:[
          {type: 'number', required: true, message: '请选择借用状态', trigger: 'change'}
        ]
      },
      loading: true,
      showEditDialog: false,
      isEdit: false,

      applyItem: {},
      rules_apply: {
        use:[
          {required: true, message: '请输入申请用途', trigger: 'blur'}
        ],
        start: [
          { required: true, message: '请选择开始日期', trigger: 'change' }
        ],
        end: [
          { required: true, message: '请选择结束时间', trigger: 'change' }
        ]
      },
      showApplyDialog: false
    }
  },
  computed:{
    rows_inShow(){
      let arr = [];
      this.rows.forEach(item => {
        let obj = {}
        for(let key in item){
          if(key === 'car_status'){
            obj[key] = item[key]?'押车':'拖车';
          }else if(key === 'borrow_status'){
            obj[key] = item[key]?'已被借用':'可以借用';
          }else{
           obj[key] = item[key]
          }
        }
        arr.push(obj)
      })
      return arr;
    }
  },
  methods:{
    fetchList(){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car/list',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'GET'
      }).then(res => {
        this.rows = res.data.data;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    addItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car/create',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '新增车辆信息成功，请在列表中查看！',
          type: 'success'
        });
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    updateItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      delete data.index;
      return rest({
        url: '/v1/inspect/'+ uid +'/car/update',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '编辑车辆信息成功，请在列表中查看！',
          type: 'success'
        });
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    deleteItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car/del',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '删除车辆信息成功，请在列表中查看！',
          type: 'success'
        });
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    addApplyItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/car_apply/create',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '新增车辆申请成功，请在往车辆申请列表中查看！',
          type: 'success'
        });
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    onAddEvent(){
      this.item = {
        borrow_status: null,
        brand: "",
        car_license:"",
        car_status: null,
        color: "",
        engine_number:"",
        frame_number:"",
        model: ""
      };
      this.isEdit = false;
      this.showEditDialog = true;
      this.$refs['itemForm']?this.$refs['itemForm'].clearValidate():null;
    },
    onEditEvent(index){
      this.item = {
        index: index,
        car_id: this.rows[index].car_id,
        borrow_status: this.rows[index].borrow_status,
        brand: this.rows[index].brand,
        car_license: this.rows[index].car_license,
        car_status: this.rows[index].car_status,
        color: this.rows[index].color,
        engine_number: this.rows[index].engine_number,
        frame_number: this.rows[index].frame_number,
        model: this.rows[index].model
      }
      this.isEdit = true;
      this.showEditDialog = true;
      this.$refs['itemForm']?this.$refs['itemForm'].clearValidate():null;
    },
    onDialogComfirm(){
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          if(this.isEdit){
            this.rows[this.item.index].index = this.item.index;
            if(equalObject(this.rows[this.item.index], this.item)){
              this.$message('无任何修改！')
              return false;
            }
            this.updateItem(this.item).then(res => {
              this.showEditDialog = false;
              this.fetchList();
            })
          }else{
            this.addItem(this.item).then(res => {
              this.showEditDialog = false;
              this.fetchList();
            });
          }
        }else{
          return false;
        }
      });
    },
    onDelEvent(index){
      this.$confirm('本次操作将永久删除该车辆信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteItem({car_id: this.rows[index].car_id}).then(res => {
          this.fetchList();
        });
      })
    },
    onApplyEvent(index){
      this.applyItem = {
        car_id: this.rows[index].car_id,
        use: "",
        start: null,
        end: null
      }
      this.showApplyDialog = true;
      this.$refs['applyItemForm']?this.$refs['applyItemForm'].clearValidate():null;
    },
    onApplyDialogComfirm(){
      this.$refs['applyItemForm'].validate((valid) => {
        if(valid){
          this.addApplyItem(this.applyItem).then(res => {
              this.showApplyDialog = false;
              this.fetchList();
            });
        }else{
          return false;
        }
      })
    }
  },
  mounted(){
    this.fetchList();
  }
}
</script>




